import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
// const PDK = require('https://assets.pinterest.com/sdk/sdk.js');
declare var PDK: any;
import '../../../sdk.js';


@Injectable()
export class AuthService {
	authToken: any;
	user: any;

  constructor(private http: Http) { }

  	registerUser(user){
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');
  		return this.http.post('users/register', user, {headers: headers})
  		.map(res => res.json());
	  }


  	authenticateUser(user){
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');
  		return this.http.post('users/authenticate', user, {headers: headers})
  		.map(res => res.json());
	  }

	  getProfile(){
	  	let headers = new Headers();
	  	this.loadToken();
  		headers.append('Authorization', this.authToken);
  		headers.append('Content-Type', 'application/json');
			console.log(headers);	  	
  		return this.http.get('users/profile', {headers: headers})
  			.map(res => res.json());
	  }

	  storeUserData(token, user) {
	  	localStorage.setItem('id_token', token);
	  	localStorage.setItem('user', JSON.stringify(user));
	  	this.authToken = token;
	  	this.user = user;
	  }

	  loadToken(){
	  	const token = localStorage.getItem('id_token');
	  	this.authToken = token;
	  }

	  loggedIn(){
	  	return tokenNotExpired();
	  }

	  logout(){
	  	this.authToken = null;
	  	this.user = null;
	  	localStorage.clear();
	  }

	  pinterest(){
	  	console.log('pinterest test');
	  	console.log(PDK);
	  	// login in pinterest
	  	// Initialize once with app id
			PDK.init({ appId: '4888245970348816308', cookie: true });
			PDK.login({ scope : 'read_public, write_public' }, console.log);
			console.log(PDK.getSession());
	  }

}



